<template>
	<div>
		<v-card>
			<div>
				<small>Total : {{ summary.totalIncome }}</small>
			</div>
			<div>
				<small>PERP : {{ summary.totalIncomePerp }}</small
				>, <small>USDT : {{ summary.totalIncomeUsdt }}</small
				>,<small> BUSD : {{ summary.totalIncomeBusd }}</small>
			</div>
		</v-card>

		<canvas-view ref="canvasView" @click-position="clickPosition"></canvas-view>

		<v-dialog v-model="showDialog" width="500">
			<v-card>
				<v-card-title dense class="grey lighten-2"> {{ selectedItem.symbol }} </v-card-title>

				<v-row class="mt-5 ml-5 mr-5">
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						${{ selectedItem.price }}
					</v-col>
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<v-btn
							depressed
							x-small
							class="rounded-r-0"
							:color="selectedItem.position === 'LONG' ? 'green lighten-3' : null"
							@click="selectedItem.position = 'LONG'"
							><small>LONG</small></v-btn
						>
						<v-btn
							depressed
							x-small
							class="rounded-l-0"
							:color="selectedItem.position === 'SHORT' ? 'red lighten-3' : null"
							@click="selectedItem.position = 'SHORT'"
							><small>SHORT</small></v-btn
						>
					</v-col>
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<v-text-field v-model="selectedItem.amt" label="amt"></v-text-field>
					</v-col>
				</v-row>

				<v-row class="mt-5 ml-5 mr-5">
					<v-col cols="4" class="mt-0 pt-0 mb-0 pb-0">
						<v-text-field v-model="selectedItem.sell_percent" label="sell_percent"></v-text-field>
					</v-col>
				</v-row>

				<v-divider></v-divider>

				<v-card-actions>
					<v-spacer></v-spacer>
					<v-btn
						class="ma-2 mr-5"
						depressed
						color="red lighten-2"
						small
						@click="clickDeleteOrder(selectedItem)"
					>
						Delete
					</v-btn>
					<v-btn
						class="ma-2"
						depressed
						color="primary lighten-1"
						small
						@click="clickAddNewOrder(selectedItem)"
					>
						Add new order
					</v-btn>
				</v-card-actions>
			</v-card>
		</v-dialog>
	</div>
</template>

<script>
import HttpService from '@/services/HttpService'
import CanvasView from './CanvasView'
import UserService from '@/services/UserService'

export default {
	name: 'Mulbot',

	components: {
		CanvasView,
	},

	data: () => ({
		showDialog: false,
		hasPermission: false,

		// orderHeaders: [
		// 	{ text: 'symbol', value: 'symbol' },
		// 	{ text: 'position', value: 'position' },
		// 	{ text: 'percent', value: 'percent' },
		// 	{ text: 'status', value: 'status' },
		// 	{ text: 'price', value: 'price' },
		// 	{ text: 'amt', value: 'amt' },
		// 	{ text: 'buyPrice', value: 'buyPrice' },
		// 	{ text: 'markPrice', value: 'markPrice' },
		// 	{ text: 'sellPercent', value: 'sell_percent' },
		// ],

		selectedItem: {},
		summary: { totalIncomeBusd: 0, totalIncomeUsdt: 0, totalIncomePerp: 0 },

		orders: [],
		symbolSummary: {},
	}),

	computed: {},

	watch: {},

	created() {
		// this.initialize()
		this.initWorker()
	},

	methods: {
		async initWorker() {
			await this.getHistories()
			await this.getOrderList()

			const user = await UserService.getUser(this.$store.state.telegramId)
			this.checkUserPermission(user)

			this.$refs.canvasView.createChartData(this.orders, this.symbolSummary)
			// this.$refs.sliderView.createChartData(this.orders, this.symbolSummary)
			// this.$refs.apexBubbleView.createBubbleChartData(this.orders, this.symbolSummary)
		},

		checkUserPermission(user) {
			for (const item of user) {
				if (item.bot_name === 'Tribot' && item.exchange === 'Binance') {
					this.hasPermission = true
				}
			}
		},

		async getHistories() {
			let totalIncomeBusd = 0
			let totalIncomeUsdt = 0
			let totalIncomePerp = 0

			let histories = await HttpService.get('/coin/binance/tribot/histories')
			// console.log(histories)

			for (let item of histories) {
				let lastChars = item.symbol.substr(-4)
				if (lastChars === 'BUSD') {
					let earn = 0
					if (item.position === 'LONG') {
						let percent = (item.sell_price / item.buy_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					} else {
						let percent = (item.buy_price / item.sell_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					}

					totalIncomeBusd += earn
				} else if (lastChars === 'USDT') {
					let earn = 0
					if (item.position === 'LONG') {
						let percent = (item.sell_price / item.buy_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					} else {
						let percent = (item.buy_price / item.sell_price - 1) * 100
						earn = item.buy_price * item.amt * percent * 0.01
					}

					totalIncomeUsdt += earn
				} else if (lastChars === 'PERP') {
					let earn = 0
					let convertedUsd = 0
					if (item.symbol === 'BTCUSD_PERP') {
						convertedUsd = 100
					} else {
						convertedUsd = 10
					}

					if (item.position === 'LONG') {
						let percent = (item.sell_price / item.buy_price - 1) * 100
						earn = convertedUsd * percent * 0.01
					} else {
						let percent = (item.buy_price / item.sell_price - 1) * 100
						earn = convertedUsd * percent * 0.01
					}

					totalIncomePerp += earn
				}
			}

			this.summary.totalIncomeUsdt = Number(totalIncomeUsdt.toFixed(1))
			this.summary.totalIncomeBusd = Number(totalIncomeBusd.toFixed(1))
			this.summary.totalIncomePerp = Number(totalIncomePerp.toFixed(1))
			this.summary.totalIncome = (totalIncomeUsdt + totalIncomeBusd + totalIncomePerp).toFixed(1)
		},

		async getOrderList() {
			let orders = await HttpService.get('/coin/binance/tribot/orders')
			let positions = await HttpService.get('/coin/binance/tribot/positions')
			let positionsRisk = await HttpService.get('/coin/binance/tribot/positions/risk')

			// console.log(positionsRisk)

			for (let item of orders) {
				if (this.symbolSummary[item.symbol] === undefined) {
					this.symbolSummary[item.symbol] = {
						minPrice: 999999,
						maxPrice: 0,
						nowPrice: 0,
						name: item.symbol,
						data: [],
						data2: {},
					}
				}
			}

			for (let item of orders) {
				let position = this.$underscore.find(positions, { tribot_order_id: item.id })
				let positionRisk = this.$underscore.find(positionsRisk, { symbol: item.symbol })

				if (position !== undefined) {
					item.buyPrice = position.buy_price

					if (position.sell_order_code === null) {
						item.status = position.buy_order_status
					} else {
						item.status = position.sell_order_code
					}

					this.symbolSummary[item.symbol].status = item.status
					this.symbolSummary[item.symbol].position = item.position
				} else {
					item.status = '-'
				}

				if (positionRisk) {
					let markPrice = Number(positionRisk.markPrice)

					if (this.symbolSummary[item.symbol].minPrice > item.buyPrice) {
						this.symbolSummary[item.symbol].minPrice = item.buyPrice
					}

					if (this.symbolSummary[item.symbol].maxPrice < item.buyPrice) {
						this.symbolSummary[item.symbol].maxPrice = item.buyPrice
					}

					if (this.symbolSummary[item.symbol].minPrice > markPrice) {
						this.symbolSummary[item.symbol].minPrice = markPrice
					}

					this.symbolSummary[item.symbol].nowPrice = markPrice

					if (item.position === 'LONG') {
						let positionPercent = (markPrice / item.buyPrice - 1) * 100
						item.percent = positionPercent
					} else if (item.position === 'SHORT') {
						let positionPercent = (item.buyPrice / markPrice - 1) * 100
						item.percent = positionPercent
					}

					item.percent = item.percent.toFixed(1)
					if (isNaN(item.percent)) {
						item.percent = ''
					}

					this.symbolSummary[item.symbol].percent = item.percent

					if (item.symbol.includes('BTC')) {
						item.markPrice = markPrice.toFixed(0)
					} else if (item.symbol.includes('ETH')) {
						item.markPrice = markPrice.toFixed(0)
					} else if (item.symbol.includes('BNB')) {
						item.markPrice = markPrice.toFixed(1)
					} else if (item.symbol.includes('XRP')) {
						item.markPrice = markPrice.toFixed(3)
					} else if (item.symbol.includes('BCH')) {
						item.markPrice = markPrice.toFixed(1)
					} else if (item.symbol.includes('MKR')) {
						item.markPrice = markPrice.toFixed(0)
					}

					let convertedPrice = 0

					if (item.symbol === 'BTCUSD_PERP') {
						convertedPrice = 100
					} else if (item.symbol === 'ETHUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'BNBUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'XRPUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'ADAUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'DOTUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'TRXUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'LTCUSD_PERP') {
						convertedPrice = 10
					} else if (item.symbol === 'MATICUSD_PERP') {
						convertedPrice = 10
					}

					if (item.symbol.includes('PERP')) {
						item.price = convertedPrice.toFixed(1)
					} else {
						item.price = (item.amt * markPrice).toFixed(1)
					}
				}
			}
			this.orders = orders
		},

		clickPosition(item) {
			if (this.hasPermission) {
				this.showDialog = true
				this.selectedItem = item
			}
		},

		async clickAddNewOrder(item) {
			// console.log(item)
			if (item) {
				const result = await HttpService.post('/coin/binance/tribot/orders', item)
				this.getOrderList()
				this.showDialog = false
			}
		},

		async clickDeleteOrder(item) {
			if (item.status !== 'FILLED') {
				// console.log('can delete')
				await HttpService.delete('/coin/binance/tribot/orders/' + item.id)
				this.getOrderList()
				this.showDialog = false
			}
		},

		toFixed: function (val, num) {
			return Number(Number(val).toFixed(num))
		},
	},
}
</script>

