		testAmtProfit(rounds) {
			let sumIncomeWin = 0
			let sumIncomeLose = 0
			let sumTestIncomeWin = 0
			let sumTestIncomeLose = 0
			let sumTestIncomeAdv = 0
			let sumTestIncomeAdvLose = 0

			let amts_test1 = [0.003, 0.006, 0.0137, 0.0277, 0, 0, 0, 0, 0]
			let amts_test2 = [0, 0.02, 0.04, 0, 0, 0, 0, 0, 0, 0]

			for (let item of rounds) {
				// if (item.epoch > 58490) {
				let fee = 0.00047
				// if ( (item.up_down === item.position || item.up_down === item.position_adv) && item.amt > 0) {
				if (item.up_down === item.order_position && item.amt > 0) {
					let payout = 0

					if (item.up_down === 'UP') {
						item.payout = item.bear_amt / item.bull_amt + 1
						payout = item.bear_amt / item.bull_amt + 1
					} else {
						item.payout = item.bull_amt / item.bear_amt + 1
						payout = item.bull_amt / item.bear_amt + 1
					}

					item.income = item.amt * payout * 0.97 - item.amt
					if (item.income > 0) item.income += fee
					sumIncomeWin += item.income

					if (item.amt > 0) {
						this.summary.sumPayout += payout * 0.97
						this.summary.payoutCount++
					}
				} else if (item.amt > 0) {
					item.income = -item.amt
					if (item.income > 0) item.income += fee

					sumIncomeLose += item.income
				}

				let testAmt = Number(amts_test1[item.position_index - 1])
				let testAmt2 = Number(amts_test1[item.position_index_adv - 1])
				if (isNaN(testAmt)) {
					testAmt = 0
				}

				if (isNaN(testAmt2)) {
					testAmt2 = 0
				}

				if ((item.position_1 === 'UP' || item.position_1 === 'DOWN') && testAmt > 0) {
					if (item.up_down === item.position_1) {
						let payout = 0

						if (item.up_down === 'UP') {
							payout = item.bear_amt / item.bull_amt + 1
						} else {
							payout = item.bull_amt / item.bear_amt + 1
						}

						let testIncome1 = testAmt * payout * 0.97 - testAmt

						if (testIncome1 > 0) testIncome1 += fee

						sumTestIncomeWin += testIncome1

						// console.log(item.epoch,' :',testIncome1)
					} else {
						let testIncome1 = -testAmt

						if (testIncome1 > 0) testIncome1 += fee

						// console.log(item.epoch, ' :', testIncome1, ' amt:', testAmt)

						sumTestIncomeLose += testIncome1
					}
				}

				if ((item.position_adv === 'UP' || item.position_adv === 'DOWN') && testAmt2 > 0) {
					if (item.up_down === item.position_adv) {
						let payout = 0
						if (item.up_down === 'UP') {
							payout = item.bear_amt / item.bull_amt + 1
						} else {
							payout = item.bull_amt / item.bear_amt + 1
						}

						let testIncome2 = testAmt2 * payout * 0.97 - testAmt2
						if (testIncome2 > 0) testIncome2 += fee
						sumTestIncomeAdv += testIncome2
						// console.log(item.epoch,' :',testIncome1)
					} else {
						let testIncome2 = -testAmt2
						if (testIncome2 > 0) testIncome2 += fee
						// console.log(item.epoch, ' :', testIncome2, ' amt:', testAmt2)
						sumTestIncomeAdvLose += testIncome2
					}
				}
			}

			this.summary.sumIncomeWin = sumIncomeWin
			this.summary.sumTestIncomeWin = sumTestIncomeWin
			this.summary.sumTestIncomeAdv = sumTestIncomeAdv

			this.summary.sumIncomeLose = sumIncomeLose
			this.summary.sumTestIncomeLose = sumTestIncomeLose
			this.summary.sumTestIncomeAdvLose = sumTestIncomeAdvLose

			// console.log(this.summary.sumTestIncomeWin, this.summary.sumTestIncomeLose)
		},

