		testFinal(rounds) {
			this.summary.testWinCount = 0
			this.summary.testCount = 0

			for (let i = rounds.length - 1; i >= 0; i--) {
				let position = ''

				if (rounds[i].position_index_adv === 1) {
					position = rounds[i].position_adv
				} else if (rounds[i].position_index_adv === 2) {
					position = rounds[i].position_adv
				} else if (rounds[i].position_index_adv === 6) {
					position = rounds[i].position_adv
				}

				rounds[i].position_1 = position
			}

			for (let i = rounds.length - 11; i >= 0; i--) {
				let frame = this.getFrame(rounds, i + 2, 10)
				let findItem1 = []
				let targetEpoch = rounds[i].epoch % 2

				for (let item of frame) {
					if (
						item &&
						(item.position_1 === 'UP' || item.position_1 === 'DOWN') &&
						item.epoch % 2 === targetEpoch
					) {
						findItem1.push(item)
					}
				}

				findItem1 = findItem1[0]
				if (findItem1 !== undefined) {
					if (findItem1.position_1 === 'UP' || findItem1.position_1 === 'DOWN') {
						if (findItem1.up_down === findItem1.position_1) {
							rounds[i].position_index_1 = 1
						} else {
							rounds[i].position_index_1 = findItem1.position_index_1 + 1
						}
					} else {
						rounds[i].position_index_1 = findItem1.position_index_1
					}
				}
			}

			for (let i = rounds.length - 3; i >= 0; i--) {
				if (rounds[i + 2].position_1 === 'UP' || rounds[i + 2].position_1 === 'DOWN') {
					this.summary.testCount++
					if (rounds[i + 2].up_down === rounds[i + 2].position_1) {
						this.summary.testWinCount++
						let objKey = 'i' + rounds[i + 2].position_index_1
						this.summary.winCount[4][objKey]++
					}
					if (rounds[i + 2].up_down !== rounds[i + 2].position_1) {
						let objKey = 'i' + rounds[i + 2].position_index_1
						this.summary.winCount[5][objKey]++
					}
				}
			}

			for (let i = 1; i < 6; i++) {
				let objKey = 'i' + i
				let percent =
					(this.summary.winCount[4][objKey] /
						(this.summary.winCount[4][objKey] + this.summary.winCount[5][objKey])) *
					100
				percent = percent.toFixed(2)
				this.summary.winCount[4][objKey] = this.summary.winCount[4][objKey] + ' (' + percent + ')'
			}

			return rounds
		},

		testAdv(rounds) {
			this.summary.reverseWinCount = 0
			this.summary.reverseCount = 0

			for (let i = rounds.length - 1; i >= 0; i--) {
				let position = ''

				let bullGap30 = rounds[i].bull_amt3 - rounds[i].bull_amt0
				let bullGap31 = rounds[i].bull_amt3 - rounds[i].bull_amt1
				let bullGap32 = rounds[i].bull_amt3 - rounds[i].bull_amt2
				let bearGap30 = rounds[i].bear_amt3 - rounds[i].bear_amt0
				let bearGap31 = rounds[i].bear_amt3 - rounds[i].bear_amt1
				let bearGap32 = rounds[i].bear_amt3 - rounds[i].bear_amt2

				// if (rounds[i].bull_amt3 > rounds[i].bear_amt3) {
				// 	if (rounds[i].bull_amt2 > rounds[i].bear_amt2) {
				// 		if (rounds[i].bull_amt0 > rounds[i].bear_amt0) {
				// 			position = 'UP'
				// 		}
				// 	}
				// }

				if (bearGap32 > bullGap31) {
					position = 'DOWN'
				}

				// if (rounds[i].bear_amt3 > rounds[i].bull_amt3) {
				// 	if (rounds[i].bear_amt2 > rounds[i].bull_amt2) {
				// 		if (rounds[i].bear_amt0 > rounds[i].bull_amt0) {
				// 			position = 'DOWN'
				// 		}
				// 	}
				// }

				if (bullGap32 > bearGap31) {
					position = 'UP'
				}
				rounds[i].position_adv = position
			}

			for (let i = rounds.length - 3; i >= 0; i--) {
				if (rounds[i + 2].position_adv === 'UP' || rounds[i + 2].position_adv === 'DOWN') {
					if (rounds[i + 2].up_down === rounds[i + 2].position_adv) {
						rounds[i].position_index_adv = 1
					} else {
						rounds[i].position_index_adv = rounds[i + 2].position_index_adv + 1
					}
				} else {
					rounds[i].position_index_adv = rounds[i + 2].position_index_adv
				}

				if (rounds[i + 2].position_adv === 'UP' || rounds[i + 2].position_adv === 'DOWN') {
					this.summary.reverseCount++
					if (rounds[i + 2].up_down === rounds[i + 2].position_adv) {
						this.summary.reverseWinCount++
						let objKey = 'i' + rounds[i + 2].position_index_adv
						this.summary.winCount[2][objKey]++
					}
					if (rounds[i + 2].up_down !== rounds[i + 2].position_adv) {
						let objKey = 'i' + rounds[i + 2].position_index_adv
						this.summary.winCount[3][objKey]++
					}
				}
			}


			for (let i = 1; i < 6; i++) {
				let objKey = 'i' + i
				let percent =
					(this.summary.winCount[2][objKey] /
						(this.summary.winCount[2][objKey] + this.summary.winCount[3][objKey])) *
					100
				percent = percent.toFixed(2)
				this.summary.winCount[2][objKey] = this.summary.winCount[2][objKey] + ' (' + percent + ')'
			}



			return rounds
		},

		testFollowWinIndex(rounds) {
			this.summary.followWinCount = 0
			this.summary.followCount = 0

			for (let i = rounds.length - 3; i >= 0; i--) {
				let position = ''

				if (rounds[i + 2].bull_amt > 0) {
					position = rounds[i + 2].bull_amt > rounds[i + 2].bear_amt ? 'UP' : 'DOWN'
					rounds[i + 2].position_follow = position
				}

				if (position === 'UP' || position === 'DOWN') {
					this.summary.followCount++
				}

				if (rounds[i + 2].up_down === position) {
					rounds[i].follow_win_index = 1
					this.summary.followWinCount++
				} else if (position === 'UP' || position === 'DOWN') {
					rounds[i].follow_win_index = rounds[i + 2].follow_win_index + 1
				} else {
					rounds[i].follow_win_index = rounds[i + 2].follow_win_index
				}
			}
			return rounds
		},

		getFrame(rounds, start, size) {
			let list = []
			for (let i = start; i < start + size; i++) {
				list.push(rounds[i])
			}
			return list
		},

		clickAmtTest() {
			this.testAmtProfit(this.rounds)
		},

		async clickUpdateSetting() {
			await HttpService.put('/pish/prediction/rounds/settings', this.settings)
		},

		async clickApplyTest(nameNum, amtTest) {
			let numList = amtTest.map((x) => Number(x))
			let name = ''
			if (nameNum === 1) {
				name = 'index1_test1'
			} else if (nameNum === 2) {
				name = 'index1_test2'
			} else if (nameNum === 3) {
				name = 'index1_reverse_test1'
			}

			await HttpService.put('/pish/prediction/amts', { name: name, amt_list: numList })
			this.setAmts()
		},

		getPercent(total, count) {
			return ((count / total) * 100).toFixed(1)
		},

		winColor(up_down, position, index) {
			let color = 'white lighten-3'

			if (up_down === position) {
				if (position === 'UP') {
					if (index >= 7) {
						color = 'green lighten-1'
					} else if (index >= 6) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 3) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else if (position === 'DOWN') {
					if (index >= 7) {
						color = 'red lighten-1'
					} else if (index >= 6) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 3) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			}

			return color
		},

		winColor2(up_down, position, index) {
			let color = 'white lighten-3'

			if (up_down === position) {
				if (position === 'UP') {
					if (index >= 8) {
						color = 'green lighten-1'
					} else if (index >= 7) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 3) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else if (position === 'DOWN') {
					if (index >= 8) {
						color = 'red lighten-1'
					} else if (index >= 7) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 3) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			} else {
				if (position === 'UP' || position === 'DOWN') {
					if (index >= 1) {
						color = 'amber lighten-3'
					}
				}
			}

			return color
		},

		followColor(up_down, position, index) {
			let color = 'white lighten-3'

			if (up_down === position) {
				if (up_down === 'UP') {
					color = 'green lighten-4'
				} else {
					color = 'red lighten-4'
				}
			}

			return color
		},

		plainColor(index) {
			let color = 'white lighten-3'

			if (index.epoch % 2 === 0) {
				if (index.plain_win_index >= 6) {
					color = 'green lighten-2'
				} else if (index.plain_win_index >= 5) {
					color = 'green lighten-3'
				} else if (index.plain_win_index >= 4) {
					color = 'green lighten-4'
				} else if (index.plain_win_index >= 3) {
					color = 'green lighten-5'
				}
			} else {
				if (index.plain_win_index >= 6) {
					color = 'red lighten-2'
				} else if (index.plain_win_index >= 5) {
					color = 'red lighten-3'
				} else if (index.plain_win_index >= 4) {
					color = 'red lighten-4'
				} else if (index.plain_win_index >= 3) {
					color = 'red lighten-5'
				}
			}

			return color
		},

		upDownColor(item) {
			let color = 'grey lighten-4'
			if (item.up_down === 'UP') {
				color = 'green lighten-4'
			} else if (item.up_down === 'DOWN') {
				color = 'red lighten-4'
			} else if (item.up_down === 'BURN') {
				color = 'amber lighten-3'
			}
			return color
		},

		cToFixed(num, size) {
			try {
				return num.toFixed(size)
			} catch (e) {
				return ''
			}
		},
	},
}
</script>
