			// { text: 'down2', value: 'position_index_down2' },
			// { text: 'down3', value: 'position_index_down3' },

			// { text: 'down1', value: 'position_index_down1' },
			// { text: 'final', value: 'position_index_final' },

			// { text: 'long', value: 'position_index_long' },
			// { text: 'short', value: 'position_index_short' },

			// { text: 'ema', value: 'position_index_ema' },
			// { text: 'amtt', value: 'amt_test' },
			// { text: 'P2', value: 'plain2_win_index' },
			// { text: 'follow', value: 'position_follow' },

			// { text: 'rEma', value: 'ema_gap' },
			// { text: 'pEma', value: 'ema_gap1' },
			// { text: 'rsi', value: 'rsi' },
			{ text: 'time', value: 'lock_timestamp' },
		],

		indexHeaders: [
