		summary: {
			followWinCount: 0,
			sumTestIncomeWin: 0,
			sumTestIncomeLose: 0,
			sumTest2IncomeWin: 0,
			sumTest2IncomeLose: 0,
			winCount: [],
		},

		settings: {},
	}),

	created: function () {
		this.setAmts()
		this.getSettings()
