	},

	methods: {
		async init() {
			console.log(this.amts_test1)
			console.log(this.amts_test2)

			let rounds = await HttpService.get('/pish/prediction/rounds2')

			this.summary.followWinCount = 0
			this.summary.positionWinCount = 0
			this.summary.positionCount = 0
			this.summary.position1WinCount = 0
			this.summary.upCount = 0
			this.summary.downCount = 0

			this.summary.sumPayout = 0
			this.summary.payoutCount = 0

			this.summary.winCount = [
				{ s: 'order', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'order', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'test', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'test', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'up2', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'up2', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'down2', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'down2', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'up', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'up', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'down', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				{ s: 'down', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
				// { s: 'test2', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0 },
			]

			// { s: 'long', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
			// { s: 'long', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
			// { s: 'short', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },
			// { s: 'short', i1: 0, i2: 0, i3: 0, i4: 0, i5: 0, i6: 0, i7: 0, i8: 0, i9: 0 },

			for (let item of rounds) {
				if (item.hash) {
					item.hashShort = item.hash.substr(0, 5)
				}

				if (item.up_down === 'UP') {
					this.summary.upCount++
				} else {
					this.summary.downCount++
				}

				if (item.order_position === 'UP' || item.order_position === 'DOWN') {
					this.summary.positionCount++
					if (item.order_position === item.up_down) {
						let objKey = 'i' + item.order_index
						this.summary.winCount[0][objKey]++
						this.summary.positionWinCount++

						if (item.order_index === 1) {
							this.summary.position1WinCount++
						}
					} else {
						let objKey = 'i' + item.order_index
						this.summary.winCount[1][objKey]++
					}
				}

				let date = new Date(item.lock_timestamp).setHours(new Date(item.lock_timestamp).getHours() + 9)
				item.lock_timestamp = this.date2str(new Date(date), 'dd hh:mm')

				item.price = item.close_price - item.lock_price
			}

			// rounds = this.testPositionDown(rounds)
			rounds = this.testPositionUp(rounds)
			// rounds = this.testPositionUp2(rounds)
			rounds = this.testPositionUpDown(rounds)
			// rounds = this.testPositionRsiPlain(rounds)
			// rounds = this.testPositionUdReverse(rounds)
			// rounds = this.testPositionReverse(rounds)
			// rounds = this.testPositionGapRsi(rounds)
			// rounds = this.testPositionRsi(rounds)

			rounds = this.testPositionTest(rounds)

			this.testAmtProfit(rounds)

			console.log(this.summary.followWinCountList)
			this.rounds = rounds
		},

		testAmtProfit(rounds) {
			let sumIncomeWin = 0
			let sumIncomeLose = 0
			let sumTestIncomeWin = 0
			let sumTestIncomeLose = 0
			let sumTest2IncomeWin = 0
			let sumTest2IncomeLose = 0

			for (let item of rounds) {
				// if (item.epoch > 58490) {
				let fee = 0.00047

				let testAmt = Number(this.amts_test1[item.position_index - 1])
				let testAmt2 = Number(this.amts_test2[item.position_index - 1])
				if (isNaN(testAmt)) {
					testAmt = 0
				}

				if (isNaN(testAmt2)) {
					testAmt2 = 0
				}

				if (item.up_down === item.order_position && item.amt > 0) {
					let payout = 0

					if (item.up_down === 'UP') {
						item.payout = item.bear_amt / item.bull_amt + 1
						payout = item.bear_amt / item.bull_amt + 1
					} else {
						item.payout = item.bull_amt / item.bear_amt + 1
						payout = item.bull_amt / item.bear_amt + 1
					}

					item.income = item.amt * payout * 0.97 - item.amt
					let testIncome1 = testAmt * payout * 0.97 - testAmt
					let testIncome2 = testAmt2 * payout * 0.97 - testAmt2

					if (item.income > 0) item.income += fee
					if (testIncome1 > 0) testIncome1 += fee
					if (testIncome2 > 0) testIncome2 += fee

					sumIncomeWin += item.income
					sumTestIncomeWin += testIncome1
					sumTest2IncomeWin += testIncome2

					// console.log(item.epoch,' :',testIncome2)

					if (item.amt > 0) {
						this.summary.sumPayout += payout * 0.97
						this.summary.payoutCount++
					}
				} else if (item.amt > 0) {
					item.income = -item.amt
					// console.log(item.income)
					let testIncome1 = -testAmt
					let testIncome2 = -testAmt2

					if (item.income > 0) item.income += fee
					if (testIncome1 > 0) testIncome1 += fee
					if (testIncome2 > 0) testIncome2 += fee

					// console.log(item.epoch,' :',testIncome2)

					sumIncomeLose += item.income
					sumTestIncomeLose += testIncome1
					sumTest2IncomeLose += testIncome2
				}

				// }
			}

			this.summary.sumIncomeWin = sumIncomeWin
			this.summary.sumTestIncomeWin = sumTestIncomeWin
			this.summary.sumTest2IncomeWin = sumTest2IncomeWin

			this.summary.sumIncomeLose = sumIncomeLose
			this.summary.sumTestIncomeLose = sumTestIncomeLose
			this.summary.sumTest2IncomeLose = sumTest2IncomeLose

			console.log(this.summary.sumTestIncomeWin, this.summary.sumTestIncomeLose)
		},

		testPositionReverse(rounds) {
			let sumTestPositionEmaWin = 0
			let sumTestPositionEmaLose = 0

			// this.summary.winCount[2].i1 = 0
			// this.summary.winCount[2].i2 = 0
			// this.summary.winCount[2].i3 = 0
			// this.summary.winCount[2].i4 = 0
			// this.summary.winCount[2].i5 = 0
			// this.summary.winCount[2].i6 = 0
			// this.summary.winCount[2].i7 = 0
			// this.summary.winCount[2].i8 = 0

			// this.summary.winCount[3].i1 = 0
			// this.summary.winCount[3].i2 = 0
			// this.summary.winCount[3].i3 = 0
			// this.summary.winCount[3].i4 = 0
			// this.summary.winCount[3].i5 = 0
			// this.summary.winCount[3].i6 = 0
			// this.summary.winCount[3].i7 = 0
			// this.summary.winCount[3].i8 = 0

			this.summary.positionRsiCount = 0
			this.summary.positionRsiWinCount = 0

			for (let k = rounds.length - 1; k >= 0; k--) {
				if (rounds[k].position_rsi === 'UP' || rounds[k].position_rsi === 'DOWN') {
					let frame = this.getFrame(rounds, k + 2, 20)
					let findItem1 = []
					let targetEpoch = rounds[k].epoch % 2

					for (let item of frame) {
						if (
							item &&
							(item.position_rsi === 'UP' || item.position_rsi === 'DOWN') &&
							item.epoch % 2 === targetEpoch
						) {
							findItem1.push(item)
						}
					}

					findItem1 = findItem1[0]

					if (findItem1) {
						if (findItem1.position_rsi === 'UP' || findItem1.position_rsi === 'DOWN') {
							this.summary.positionRsiCount++
						}

						if (findItem1.up_down === findItem1.position_rsi) {
							this.summary.positionRsiWinCount++
							rounds[k].position_index_reverse = 1
						} else {
							rounds[k].position_index_reverse = findItem1.position_index_reverse + 1
						}
					}
				}
			}

			for (let i = rounds.length - 3; i >= 0; i--) {
				let fee = 0.00047

				if (rounds[i].position_rsi === 'UP' || rounds[i].position_rsi === 'DOWN') {
					let tempAmt = this.amts_test1[rounds[i].position_index_reverse - 1]

					if (rounds[i].up_down === rounds[i].position_rsi) {
						let payout = 0
						if (rounds[i].up_down === 'UP') {
							payout = rounds[i].bear_amt / rounds[i].bull_amt + 1
						} else {
							payout = rounds[i].bull_amt / rounds[i].bear_amt + 1
						}

						let testIncome2 = tempAmt * payout * 0.97 - tempAmt
						if (testIncome2 > 0) testIncome2 += fee

						if (tempAmt !== undefined) {
							sumTestPositionEmaWin += testIncome2
							rounds[i].amt_test = testIncome2
						}

						// console.log(rounds[i].epoch,' :',testIncome1)
					} else {
						let testIncome2 = -tempAmt
						if (testIncome2 > 0) testIncome2 += fee
						// console.log(rounds[i].epoch, ' :', rounds[i].position_rsi, ' , ', testIncome2, ' amt:', tempAmt)

						if (tempAmt !== undefined) {
							sumTestPositionEmaLose += testIncome2
							rounds[i].amt_test = testIncome2
						}
					}
				}

				// if (rounds[i].position_rsi !== 'SKIP') {
				// 	if (rounds[i].up_down === rounds[i].position_rsi) {
				// 		let objKey = 'i' + rounds[i].position_index_reverse
				// 		this.summary.winCount[2][objKey]++
				// 	} else {
				// 		let objKey = 'i' + rounds[i].position_index_reverse
				// 		this.summary.winCount[3][objKey]++
				// 	}
				// }
			}

			this.summary.sumPositionRsiWin = sumTestPositionEmaWin
			this.summary.sumPositionRsiLose = sumTestPositionEmaLose

			return rounds
		},

		testPositionUdReverse(rounds) {
			let sumTestPositionShortWin = 0
			let sumTestPositionShortLose = 0

			this.summary.winCount[2].i1 = 0
			this.summary.winCount[2].i2 = 0
			this.summary.winCount[2].i3 = 0
			this.summary.winCount[2].i4 = 0
			this.summary.winCount[2].i5 = 0
			this.summary.winCount[2].i6 = 0
			this.summary.winCount[2].i7 = 0
			this.summary.winCount[2].i8 = 0

			this.summary.winCount[3].i1 = 0
			this.summary.winCount[3].i2 = 0
			this.summary.winCount[3].i3 = 0
			this.summary.winCount[3].i4 = 0
			this.summary.winCount[3].i5 = 0
			this.summary.winCount[3].i6 = 0
			this.summary.winCount[3].i7 = 0
			this.summary.winCount[3].i8 = 0

			this.summary.positionUdReverseCount = 0
			this.summary.positionUdReverseWinCount = 0

			let interval = 2

			for (let i = rounds.length - 3; i >= 0; i--) {
				let position = ''

				if (rounds[i + 2].up_down === 'UP') {
					position = 'DOWN'
				} else {
					position = 'UP'
				}

				rounds[i].position_udreverse = position

				let frame = this.getFrame(rounds, i + interval, 1)

				if (frame[0] !== undefined && frame.length > 0) {
					let find = frame[0]

					if (find.position_udreverse !== 'SKIP') {
						if (find.up_down === find.position_udreverse) {
							rounds[i].position_index_udreverse = 1
						} else {
							rounds[i].position_index_udreverse = find.position_index_udreverse + 1
						}
					} else {
						rounds[i].position_index_udreverse = find.position_index_udreverse
					}
				}
			}

			for (let i = rounds.length - 13; i >= 0; i--) {
				let frame = this.getFrame(rounds, i + 2, 10)
				let findItem1 = []
				let targetEpoch = rounds[i].epoch % 2

				for (let item of frame) {
					if (item && item.epoch % 2 === targetEpoch) {
						findItem1.push(item)
					}
				}

				findItem1 = findItem1[0]

				if (findItem1) {
					if (
						findItem1.position_index_udreverse === 1 ||
						findItem1.position_index_udreverse === 3 ||
						findItem1.position_index_udreverse === 4 ||
						findItem1.position_index_udreverse === 5
					) {
						if (findItem1.up_down === findItem1.position_udreverse) {
							rounds[i].position_index_up4 = 1
						} else {
							rounds[i].position_index_up4 = findItem1.position_index_up4 + 1
						}
					} else {
						rounds[i].position_index_up4 = findItem1.position_index_up4
					}
				}
			}

			for (let i = rounds.length - 3; i >= 0; i--) {
				let fee = 0.00047

				if (rounds[i].position_udreverse === 'UP' || rounds[i].position_udreverse === 'DOWN') {
					let tempAmt = this.amts_test1[rounds[i].position_index_udreverse - 1]

					if (rounds[i].up_down === rounds[i].position_udreverse) {
						let payout = 0
						if (rounds[i].up_down === 'UP') {
							payout = rounds[i].bear_amt / rounds[i].bull_amt + 1
						} else {
							payout = rounds[i].bull_amt / rounds[i].bear_amt + 1
						}

						let testIncome2 = tempAmt * payout * 0.97 - tempAmt
						if (testIncome2 > 0) testIncome2 += fee

						if (tempAmt !== undefined) {
							sumTestPositionShortWin += testIncome2
							rounds[i].amt_test = testIncome2
						}

						// console.log(rounds[i].epoch,' :',testIncome1)
					} else {
						let testIncome2 = -tempAmt
						if (testIncome2 > 0) testIncome2 += fee
						// console.log(rounds[i].epoch, ' :', rounds[i].position_udreverse, ' , ', testIncome2, ' amt:', tempAmt)

						if (tempAmt !== undefined) {
							sumTestPositionShortLose += testIncome2
							rounds[i].amt_test = testIncome2
						}
					}
				}

				if (rounds[i].position_udreverse !== 'SKIP') {
					this.summary.positionUdReverseCount++
					if (rounds[i].up_down === rounds[i].position_udreverse) {
						this.summary.positionUdReverseWinCount++
						let objKey = 'i' + rounds[i].position_index_udreverse
						this.summary.winCount[2][objKey]++
					} else {
						let objKey = 'i' + rounds[i].position_index_udreverse
						this.summary.winCount[3][objKey]++
					}
				}
			}

			for (let i = 1; i < 6; i++) {
				let objKey = 'i' + i
				let percent =
					(this.summary.winCount[2][objKey] /
						(this.summary.winCount[2][objKey] + this.summary.winCount[3][objKey])) *
					100
				percent = percent.toFixed(2)
				this.summary.winCount[2][objKey] = this.summary.winCount[2][objKey] + ' (' + percent + ')'
			}

			this.summary.sumPositionUdReverseWin = sumTestPositionShortWin
			this.summary.sumPositionUdReverseLose = sumTestPositionShortLose

			return rounds
		},

		testPositionDown(rounds) {
			let sumTestPositionShortWin = 0
			let sumTestPositionShortLose = 0

			this.summary.winCount[10].i1 = 0
			this.summary.winCount[10].i2 = 0
			this.summary.winCount[10].i3 = 0
			this.summary.winCount[10].i4 = 0
			this.summary.winCount[10].i5 = 0
			this.summary.winCount[10].i6 = 0
			this.summary.winCount[10].i7 = 0
			this.summary.winCount[10].i8 = 0

			this.summary.winCount[11].i1 = 0
			this.summary.winCount[11].i2 = 0
			this.summary.winCount[11].i3 = 0
			this.summary.winCount[11].i4 = 0
