		winFinalColor(up_down, index, up, down, status) {
			let color = 'white lighten-3'

			if (up_down === up) {
				if (up_down === 'UP') {
					if (index >= 6) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 4) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else {
					if (index >= 6) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 4) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			} else if (up_down === down) {
				if (up_down === 'UP') {
					if (index >= 6) {
						color = 'green lighten-2'
					} else if (index >= 5) {
						color = 'green lighten-3'
					} else if (index >= 4) {
						color = 'green lighten-4'
					} else if (index >= 1) {
						color = 'green lighten-5'
					}
				} else {
					if (index >= 6) {
						color = 'red lighten-2'
					} else if (index >= 5) {
						color = 'red lighten-3'
					} else if (index >= 4) {
						color = 'red lighten-4'
					} else if (index >= 1) {
						color = 'red lighten-5'
					}
				}
			}
			return color
		},

		statusColor(status) {
			let color = 'white lighten-4'
			if (status === 'TX_SUCCESS') {
				color = 'green lighten-4'
			} else if (status === 'TX_ERROR') {
				color = 'red lighten-4'
			} else if (status === 'NONE') {
				color = 'white lighten-4'
			} else if (status === 'SKIP') {
				color = 'amber lighten-4'
			}

			return color
		},

		upDownColor(item) {
			let color = 'grey lighten-4'
			if (item.up_down === 'UP') {
				color = 'green lighten-5'
			} else if (item.up_down === 'DOWN') {
				color = 'red lighten-5'
			} else if (item.up_down === 'BURN') {
				color = 'amber lighten-5'
			}
			return color
		},
		date2str(x, y) {
			var z = {
				M: x.getMonth() + 1,
				d: x.getDate(),
				h: x.getHours(),
				m: x.getMinutes(),
				s: x.getSeconds(),
			}
			y = y.replace(/(M+|d+|h+|m+|s+)/g, function (v) {
				return ((v.length > 1 ? '0' : '') + z[v.slice(-1)]).slice(-2)
			})

			return y.replace(/(y+)/g, function (v) {
				return x.getFullYear().toString().slice(-v.length)
			})
		},
		cToFixed(num, size) {
			try {
				return num.toFixed(size)
			} catch (e) {
				return ''
			}
		},
		cSubstr(txt, index) {
			let tResult = ''
			try {
				tResult = txt.substring(0, index).toLowerCase()
			} catch (e) {
				console.log('error')
			}
			return tResult
		},
	},
}
</script>
